.cruises {
  width: 100%;
  min-height: 682px;
  background-color: $color-alice-blue;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cruises__container {
  max-width: 1366px;
  width: 100%;
  padding: 86px 118px 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cruises h2 {
  color: $color-tangaroa;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin-top: 0;
  margin-bottom: 48px;
}

.cruises__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 30px;
  margin: 0;
  padding: 0;
}


@media (max-width: $vp-tablet) {
  .cruises__container {
    max-width: 768px;
    padding: 89px 60px 97px;
  }

  .cruises h2 {
    font-size: 26px;
    line-height: 39px;
  }
}


@media (max-width: $vp-mobile) {
  .cruises__container {
    max-width: 767px;
    min-width: 320px;
    width: 100%;
    padding: 56px 25px 66px;
  }

  .cruises h2 {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 31px;
  }

  .cruises__list {
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: space-around;
    gap: 30px;
  }
}
