.contacts {
  width: 100%;
  min-height: 506px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contacts br {
  display: none;
}

.contacts__container {
  max-width: 1366px;
  width: 100%;
  padding: 120px 103px;
  display: flex;
  align-items: flex-start;
  column-gap: 100px;
}

.contacts__map {
  width: 560px;
  height: 306px;
  background-image: url("../img/map.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
}

.contacts address {
  min-height: 306px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contacts p {
  font-weight: 500;
  font-size: 18px;
  font-style: normal;
  line-height: 27px;
  color: $color-tangaroa;
  max-width: 500px;
  margin-top: 0;
  margin-bottom: 32px;
}

.contacts ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.contacts li {
  display: flex;
  align-items: center;
}

.contacts li:not(:last-child) {
  margin-bottom: 20px;
}

.contacts__img {
  margin-right: 10px;
}

.contacts span {
  font-weight: 400;
  font-size: 16px;
  font-style: normal;
  line-height: 24px;
  color: $color-tangaroa;
  display: block;
}

@media (max-width: $vp-tablet) {
  .contacts {
    min-height: 535px;
  }

  .contacts br {
    display: block;
  }

  .contacts__container {
    min-width: 768px;
    padding: 100px 45px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
  }

  .contacts__map {
    width: 324px;
    height: 335px;
  }

  .contacts p {
    width: 324px;
    margin-bottom: 27px;
  }

  .contacts li {
    align-items: flex-start;
  }

  .contacts__img {
    width: 18px;
    height: auto;
  }
}


@media (max-width: $vp-mobile) {
  .contacts {
    min-height: 600px;
  }

  .contacts__container {
    max-width: 767px;
    min-width: 320px;
    width: 100%;
    padding: 58px 15px 58px;
    display: flex;
    flex-direction: column-reverse;
    column-gap: 0;
  }

  .contacts__map {
    max-width: 767px;
    min-width: 290px;
    width: 100%;
    height: 316px;
  }

  .contacts p {
    max-width: 767px;
    min-width: 290px;
    width: 100%;
    margin-bottom: 25px;
  }

  .contacts__img {
    width: 18px;
    height: auto;
  }

  .contacts address {
    margin-bottom: 43px;
    min-height: 224px;
  }
}
