.advantages {
  width: 100%;
  min-height: 562px;
  background-image: url("../img/bg/advantages-bg.jpg");
  background-color: $color-alice-blue;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__container {
  max-width: 1366px;
  width: 100%;
  padding: 110px 103px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages ul {
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  margin: 0;
  padding: 0;
}

.advantages h2 {
  color: $color-tangaroa;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin-top: 0;
  margin-bottom: 48px;
}

@media (max-width: $vp-tablet) {
  .advantages {
    min-height: 588px;
    background-image: url("../img/bg/advantages-bg-tablet.jpg");
  }

  .advantages__container {
    min-width: 768px;
    padding: 89px 45px;
  }

  .advantages ul {
    gap: 30px;
  }

  .advantages h2 {
    font-size: 26px;
    line-height: 39px;
  }
}


@media (max-width: $vp-mobile) {
  .advantages {
    min-height: 900px;
    background-image: url("../img/bg/advantages-bg-mobile.jpg");
  }

  .advantages__container {
    max-width: 767px;
    min-width: 320px;
    width: 100%;
    padding: 56px 15px 67px;
  }

  .advantages ul {
    grid-template-columns: 1fr;
  }

  .advantages h2 {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 31px;
  }
}
